import { BrowserRouter, Switch, Route, NavLink } from "react-router-dom"
import React from 'react'
import { IconButton, createMuiTheme, Paper, Chip, Card, CardActionArea, CardContent, CardActions, Typography, Button, CircularProgress } from '@material-ui/core'
import { Delete } from '@material-ui/icons'
import { ThemeProvider } from "@material-ui/styles"
import './App.css'

const theme = createMuiTheme({ palette: { type: "dark" } })

function getSavedItems() {
  return JSON.parse(localStorage.getItem('savedItems'))
}

function App() {
  const handleItemSave = (type, data) => {
    console.log('Saving item: ', type, data)

    var items = getSavedItems()
    items.push({ type: type, data: data })
    localStorage.setItem('savedItems', JSON.stringify(items))
  }

  return (
    <BrowserRouter basename="/jokes_advice_quotes">
      <ThemeProvider theme={theme}>
        <Paper elevation={3} className="App">
          <NavBar />
          <Switch>
            <Route path="/joke">
              <Joke onSave={handleItemSave} />
            </Route>
            <Route path="/advice">
              <Advice onSave={handleItemSave} />
            </Route>
            <Route path="/quote">
              <Quote onSave={handleItemSave} />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>
        </Paper>
      </ThemeProvider>
    </BrowserRouter>
  )
}

function NavBar() {
  return (
    <div className="navBar">
      <NavLink activeClassName="active" exact={true} to="/">Home</NavLink>
      <NavLink activeClassName="active" to="/joke">Joke,</NavLink>
      <NavLink activeClassName="active" to="/advice">Advice,</NavLink>
      <NavLink activeClassName="active" to="/quote">Quote</NavLink>
      <span>&lt;- Fetch a Random</span>
    </div>

  )
}

class Home extends React.Component {
  constructor(props) {
    super(props)

    // initialize localstorage
    if (!localStorage.getItem('savedItems'))
      localStorage.setItem('savedItems', '[]')

    this.state = {}
  }


  handleItemDelete = (e, id) => {
    console.log('Deleting Item', e.target, id)

    // remove item from DOM
    e.target.closest('.itemContainer').remove()

    // remove from localStorage
    var items = getSavedItems()
    items.splice(id, 1)
    localStorage.setItem('savedItems', JSON.stringify(items))
  }

  render() {
    return (
      <div className="home">
        <div>
          <h3>Saved Items</h3>
          {getSavedItems().map((item, i) => {
            if (item.type === 'joke')
              return <Joke data={item.data} key={i} id={i} onDelete={this.handleItemDelete} />
            else if (item.type === 'advice')
              return <Advice data={item.data} key={i} id={i} onDelete={this.handleItemDelete} />
            else if (item.type === 'quote')
              return <Quote data={item.data} key={i} id={i} onDelete={this.handleItemDelete} />
            else
              return null
          })}
        </div>
      </div>
    )
  }
}

class Joke extends React.Component {
  constructor(props) {
    super(props)

    if (!props.data) {
      this.state = { joke: null }
      this.fetchRandom()
    }
    else
      this.state = { joke: props.data }
  }

  fetchRandom = async () => {
    this.setState({ joke: null })

    const response = await fetch('https://official-joke-api.appspot.com/jokes/random')
    const data = await response.json()

    this.setState({ joke: data })
  }

  render() {
    if (!this.state.joke)
      return <CircularProgress className="preloader" />

    return (
      <Card className="itemContainer joke">
        <CardActionArea>
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">Joke <Chip className="tag" label={this.state.joke.type} color="primary" /></Typography>
            <Typography variant="body2" color="textSecondary" component="p">{this.state.joke.setup}</Typography>
            <Typography variant="body2" color="textSecondary" component="p">{this.state.joke.punchline}</Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
          {this.props.data ?
            <IconButton color="secondary" onClick={(e) => this.props.onDelete(e, this.props.id)}><Delete /></IconButton>
            :
            <div className="actionButtons">
              <Button size="small" color="primary" variant="contained" onClick={() => this.props.onSave('joke', this.state.joke)}>Save</Button>
              <Button size="small" variant="contained" onClick={() => this.fetchRandom()}>Next</Button>
            </div>
          }
        </CardActions>
      </Card>
    )
  }
}

class Advice extends React.Component {
  constructor(props) {
    super(props)

    if (!props.data){
      this.fetchRandom()
      this.state = { advice: null }
    }
    else
      this.state = { advice: props.data }

  }

  fetchRandom = async () => {
    this.setState({ advice: null })

    const response = await fetch('https://api.adviceslip.com/advice')
    const data = await response.json()

    this.setState({ advice: data.slip.advice })
  }

  render() {
    if (!this.state.advice)
      return <CircularProgress className="preloader" />

    return (
      <Card className="itemContainer advice">
        <CardActionArea>
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">Advice</Typography>
            <Typography variant="body2" color="textSecondary" component="p">{this.state.advice}</Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
        {this.props.data ?
          <IconButton color="secondary" onClick={(e) => this.props.onDelete(e, this.props.id)}><Delete /></IconButton>
          :
          <div className="actionButtons">
            <Button size="small" color="primary" variant="contained" onClick={() => this.props.onSave('advice', this.state.advice)}>Save</Button>
            <Button size="small" variant="contained" onClick={() => this.fetchRandom()}>Next</Button>
          </div>
        }
        </CardActions>
      </Card>
    )
  }
}

class Quote extends React.Component {
  constructor(props) {
    super(props)

    if (!props.data) {
      this.fetchRandom()
      this.state = { quote: null }
    }
    else
      this.state = { quote: props.data }
  }

  fetchRandom = async () => {
    this.setState({ quote: null })

    const response = await fetch('http://api.quotable.io/random')
    const data = await response.json()

    this.setState({ quote: data })
  }

  render() {
    if (!this.state.quote)
      return <CircularProgress className="preloader" />

    return (
      <Card className="itemContainer quote">
        <CardActionArea>
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              Quote
              {this.state.quote.tags.map((e, i) => <Chip className="tag" key={i} label={e} color="primary" />)}
            </Typography>
            <Typography variant="body2" color="textSecondary" component="p">"{this.state.quote.content}" - {this.state.quote.author}</Typography>
          </CardContent>
        </CardActionArea>
        <CardActions>
        {this.props.data ?
          <IconButton color="secondary" onClick={(e) => this.props.onDelete(e, this.props.id)}><Delete /></IconButton>
          :
          <div className="actionButtons">
            <Button size="small" color="primary" variant="contained" onClick={() => this.props.onSave('quote', this.state.quote)}>Save</Button>
            <Button size="small" variant="contained" onClick={() => this.fetchRandom()}>Next</Button>
          </div>
        }
        </CardActions>
      </Card>
    )
  }
}

export default App